.App {
  direction: rtl;
  margin-left: 16px;
  margin-right: 16px;
  margin-top: 100px;
}

.buttonStyle {
  width: 100%;
  height: 50px;
  justify-content: center;
  margin-bottom: 178px;
  font-size: 16px;
  color: white;
  margin-top: 30px;
  font-family: 'IranFaNum';
}

.labelStyle {
  color: gray;
  font-size: 14px;
  margin-bottom: 15px;
  font-family: 'IranFaNum';
}

.topLabelStyle {
  font-family: 'IranFaNum';
  color: gray;
  font-size: 14px;
  margin-bottom: 20px;
  text-align: center;
  margin-top: 136px;
}

.ToastStyle {
  width: 330px;
  height: 100px;
  animation-duration: 5s;
  font-family: 'IranFaNum';
}

.boxStyle {
  font-size: 14px;
  padding-left: 5px;
  direction: ltr;
  width: 100%;
  height: 50px;
  font-weight: 100;
  border-width: 1px;
  border-color: gray;
  font-family: 'IranFaNum';
}


.errorStyle {
  height: 19px;
  color: red;
  font-size: 12px;
  margin-top: 10px;
  font-family: 'IranFaNum';
}


@font-face {
  font-family: 'IranFaNum';
  font-style: normal;
  font-weight: 300;
   src:
       url('./fonts/IRANSansMobileFaNum-Bold.ttf') format('truetype'),
       url('./fonts/IRANSansMobileFaNum-Light.ttf') format('truetype'),
       url('./fonts/IRANSansMobileFaNum-Medium.ttf') format('truetype'),
       url('./fonts/IRANSansMobileFaNum-UltraLight.ttf') format('truetype'),
       url('./fonts/IRANSansMobileFaNum.ttf') format('truetype'); 
 } 